import React, { useRef, useEffect, useState } from 'react';
import '../Styles/Bloc.css';
import TextareaAutosize from 'react-textarea-autosize';

function Bloc({ change, val, keyed, id, autof, delet, styles, type, addImage}) {
  const [image, setImage] = useState('');

  const handleKeyPress = (e) => {
    if (e.nativeEvent.keyCode === 13) {
      // Handle Enter key
      if (!e.nativeEvent.shiftKey) {
        if (val === '/page') {
          e.preventDefault();
        } else {
          keyed(id);
          e.preventDefault();
        }
      }
    } else if (e.nativeEvent.keyCode === 8) {
      // Handle Backspace key
      if (val === '' && id !== 0) {
        delet(id);
      }
    } else if (e.nativeEvent.ctrlKey && e.nativeEvent.keyCode === 86) {
      // Handle Ctrl+V ke

      if (image) {

      }

    }
  };

  const handlePaste = (event) => {
    const items = event.clipboardData.items;
    for (let i = 0; i < items.length; i++) {
      if (items[i].type.indexOf('image') !== -1) {
        const blob = items[i].getAsFile();
        const reader = new FileReader();
        reader.readAsDataURL(blob);
        reader.onload = () => {
          addImage(id, reader.result)
        };
      }
    }
  };

  if (autof) {
    if(type === "text") {
      if (image) {
        return (
          //<img src={image} />
          <div>
            
            <TextareaAutosize
              onKeyDownCapture={(ev) => {
                handleKeyPress(ev);
              }}
              id="bloc"
              style={styles}
              value={val}
              onPaste={handlePaste}
              name="active"
              onChange={change}
              spellCheck={false}
            />
          </div>
        );
      } else {
        return (
          <div>
            <TextareaAutosize
              onKeyDownCapture={(ev) => {
                handleKeyPress(ev);
              }}
              id="bloc"
              style={styles}
              value={val}
              onPaste={handlePaste}
              name="active"
              onChange={change}
              spellCheck={false}
            />
          </div>
        );
      }
    }
  } else if (!autof) {
    if (image) {
      return (
        //<img src={image} />
        <div>
          <TextareaAutosize
            onKeyDownCapture={(ev) => {
              handleKeyPress(ev);
            }}
            onPaste={handlePaste}
            id="bloc"
            style={styles}
            value={val}
            onChange={change}
            spellCheck={false}
          />
        </div>
      );
    } else {
      return (
        <div>
          <TextareaAutosize
            onKeyDownCapture={(ev) => {
              handleKeyPress(ev);
            }}
            onPaste={handlePaste}
            id="bloc"
            style={styles}
            value={val}
            onChange={change}
            spellCheck={false}
          />
        </div>
      );
    }
  }
}

export default Bloc;